.accordion {
  position: relative;
  cursor: pointer;
  padding: 10px;
  padding-right: 50px;
  padding-left: 0;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-family: $main-font;
  font-size: 15px;
  transition: 0.4s;
  border-bottom: 1px solid $white-color;
  background-color: transparent;
  color: inherit;
  font-weight: 400;
  line-height: 130%;

  @include ff(
    'font-size',
    (
      320px: 16px,
      768px: 20px,
      1280px: 15px,
    )
  );

  @media (min-width: $sm) {
    padding: 22px;
    padding-right: 50px;
    padding-left: 30px;
  }

  &::before {
    content: '';
    position: absolute;
    right: 0;
    top: 50%;
    width: 25px;
    height: 25px;
    transform: translateY(-50%);
    background-repeat: no-repeat;
    background-image: url('../images/icons/plus.svg');

    @media (min-width: $sm) {
      right: 30px;
    }
  }

  &:focus,
  &:active,
  &:hover {
    outline: none;
    background-color: transparent;

    &::before {
      transition: 0.4s;
      transform: translateY(-50%) rotate(0);
    }
  }

  &.active {
    &::before {
      transition: 0.3s;
      transform: translateY(-50%) rotate(45deg);
    }
  }
}

.active,
.accordion:hover {
  outline: none;
  background-color: transparent;
}

.panel {
  flex-direction: column;
  width: 100%;
  padding: 20px;
  display: none;
  overflow: hidden;
  animation: 0.5s acctext forwards;

  & > * {
    width: 100%;
    margin-bottom: 0.5rem;
  }
}

@keyframes acctext {
  from {
    opacity: 0;
    transform: translateY(0%);
  }

  to {
    opacity: 1;
    transform: translateY(5%);
  }
}

.no-js .panel {
  display: flex;
}
