.page-stub {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  margin: 0 auto;
  max-width: $xxl;
  color: $white-color;
  background-color: $dark-color;
  overflow: hidden;

  @media (min-width: $sm) {
    @include beforeBgElement(0, auto, auto, -50%, 20%);
  }

  @media (min-width: $xl) {
    &::after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 10%;
      left: 0;
      width: 300px;
      height: 373px;
      background-image: url('../images/promo/p04.png');
      background-size: contain;
      background-repeat: no-repeat;

      @supports (
        background-image: -webkit-image-set(url('../images/promo/p04.webp') 1x)
      ) {
        background-image: url('../images/promo/p04.webp');
      }

      filter: blur(7px);
      animation: anim 3s infinite;
    }
  }

  & .container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
    max-width: $xxl;
    margin: 0 auto;
    padding: 0 15px;
    padding-top: 24px;

    @media (min-width: $xs) {
      padding-left: 45px;
      padding-right: 45px;
    }

    @media (min-width: $sm) {
      flex-direction: row;
      padding-top: 62px;
    }

    @media (min-width: $md) {
      padding-top: 75px;
    }
  }

  &__info {
    width: 100%;
    z-index: 4;
    position: relative;
    margin-bottom: 40px;
    text-align: center;

    @media (min-width: $xs) {
      text-align: left;
    }

    @media (min-width: $xl) {
      padding-left: 250px;
    }
  }

  & .title {
    max-width: 100%;
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    text-align: center;
    text-transform: uppercase;
    color: $primary-color;

    @include ff(
      'font-size',
      (
        320px: 18px,
        768px: 36px,
        1440px: 43px,
      )
    );

    @media (min-width: $sm) {
      line-height: 140%;
      text-align: left;
    }

    @media (min-width: $xl) {
      line-height: 110%;
    }
  }

  & .text {
    width: 100%;
    max-width: 100%;
    padding: 0;
    margin-bottom: 38px;
    font-weight: 700;
    font-size: 22px;
    line-height: 100%;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        320px: 22px,
        768px: 36px,
        1200px: 52px,
      )
    );
  }

  &__pic {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
  }

  & .bg-pic__pic-2 {
    position: absolute;
    bottom: 15%;
    left: 25%;
    width: 265.77px;
    height: 265.77px;
    filter: blur(7px);
    animation: anim 3s infinite;

    @media (min-width: $xs) {
      width: 409px;
      height: 409px;
      left: 50%;
      bottom: 10%;
    }

    @media (min-width: $md) {
      width: 424px;
      height: 424px;
      bottom: 20%;
    }

    @media (min-width: $xl) {
      bottom: 5%;
    }

    &::before {
      content: '';
      position: absolute;
      z-index: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;
      background-image: url('../images/promo/ellipse2.png');
      background-repeat: no-repeat;
      background-size: contain;

      @supports (
        background-image: -webkit-image-set(url('../images/promo/ellipse2.webp')
              1x)
      ) {
        background-image: url('../images/promo/ellipse2.webp');
      }
    }
  }

  & .bg-pic__pic-1 {
    position: absolute;
    top: 30%;
    right: 30%;
    width: 284.57px;
    height: 284.57px;
    filter: blur(7px);
    animation: anim 3s infinite reverse;

    @media (min-width: $xs) {
      width: 406px;
      height: 407px;
    }

    @media (min-width: $md) {
      width: 454px;
      height: 454px;
      right: 10%;
      top: 0;
    }

    &::before {
      content: '';
      position: absolute;
      z-index: 0;
      bottom: 0;
      right: -10px;
      width: 100%;
      height: 100%;
      background-image: url('../images/promo/ellipse.png');
      background-repeat: no-repeat;
      background-size: contain;

      @supports (
        background-image: -webkit-image-set(url('../images/promo/ellipse.webp')
              1x)
      ) {
        background-image: url('../images/promo/ellipse.webp');
      }
    }
  }
}
