.profit {
  position: relative;
  overflow: hidden;

  @media (min-width: $xl) {
    padding-top: 70px;
  }

  &__wrapper {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 100%;

    // @include beforeBgElement(0, auto, auto, 125%, 10%);

    // @media (min-width: $xl) {
    //   @include beforeBgElement(0, auto, auto, 45%, 10%);
    // }
  }

  & .container {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 1440px;
    padding: 40px 15px;

    @media (min-width: $md) {
      gap: 60px;
      padding-top: 0;
      padding-bottom: 54px;
    }

    // @include beforeBgElement(0, 0, auto, auto, 0);
    // @include afterBgElement(1, auto, 0, 30%, auto);
  }

  & .products {
    width: 100%;
    margin: 0 auto;
  }

  &__title {
    margin-bottom: 50px;

    @media (min-width: $sm) {
      margin-bottom: 60px;
    }

    @media (min-width: $md) {
      margin-bottom: 50px;
    }
  }

  &__title-pic {
    width: 300px;
    height: 250px;

    @media (min-width: $md) {
      // width: 430px;
      width: 330px;
      height: 200px;
      max-width: 30%;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    width: 100%;

    @media (min-width: $md) {
      gap: 40px;
      width: 60%;
    }
  }

  &__item {
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
    padding: 15px;
    margin-right: auto;
    border: 1px solid #6ddcff;
    border-radius: 10px;

    & span {
      display: inline-flex;
    }
  }

  &__item-inner {
    position: relative;
    width: 100%;
    padding-left: 60px;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      height: 50px;
      width: 50px;
      transform: translateY(-50%);
      background-image: var(--pic);
      background-repeat: no-repeat;
      background-position: 100% 0;
      background-size: contain;
    }
  }

  &__item-title {
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    color: $primary-color;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        320px: 18px,
        768px: 20px,
      )
    );
  }

  &__item-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: $white-color;

    @include ff(
      'font-size',
      (
        320px: 16px,
        768px: 20px,
      )
    );
  }
}
