.products {
  position: relative;

  @media (min-width: $sm) {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @media (min-width: $xl) {
    padding-top: 50px;
    padding-bottom: 100px;
    min-height: 800px;
  }

  @media (min-width: $xxl) {
    padding-top: 70px;
  }

  &__items {
    z-index: 3;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
    width: 100%;
    height: 100%;
    margin: 0 auto;

    @media (min-width: $md) {
      gap: 60px;
    }
  }

  & .bg-pic {
    position: absolute;

    &-3 {
      position: absolute;
      top: -3%;
      left: -47%;
      width: 420px;
      height: 420px;
      animation: anim 4s infinite reverse;

      @media (min-width: $sm) {
        left: -20%;
      }

      @media (min-width: $xl) {
        top: 30%;
        left: 15%;
      }

      &::before {
        content: '';
        position: absolute;
        z-index: 0;
        bottom: 0;
        right: 0;
        width: 420px;
        height: 420px;
        background-image: url('../images/profit/circle.png');
        background-size: contain;
        background-repeat: no-repeat;

        @supports (
          background-image: -webkit-image-set(url('../images/profit/circle.webp')
                1x)
        ) {
          background-image: url('../images/profit/circle.webp');
        }
      }
    }

    &-2 {
      z-index: 0;
      position: absolute;
      top: 35%;
      right: 12%;
      width: 237px;
      height: 237px;
      overflow: hidden;
      animation: anim 4s infinite;

      @media (min-width: $sm) {
        right: -20%;
      }

      @media (min-width: $md) {
        right: -10%;
      }

      @media (min-width: $xl) {
        top: 35%;
        right: 12%;
      }

      &::before {
        content: '';
        position: absolute;
        z-index: 1;
        bottom: 0;
        right: 0;
        width: 237px;
        height: 237px;
        background-image: url('../images/profit/ellipse2.png');
        background-size: contain;
        background-repeat: no-repeat;

        @supports (
          background-image: -webkit-image-set(url('../images/profit/ellipse2.webp')
                1x)
        ) {
          background-image: url('../images/profit/ellipse2.webp');
        }
      }
    }

    &-1 {
      z-index: 1;
      display: none;
      position: absolute;
      top: 70%;
      left: 84%;
      width: 120px;
      height: 120px;
      overflow: hidden;
      animation: anim 4s infinite reverse;

      @media (min-width: $sm) {
        display: block;
        left: -20%;
        width: 408px;
        height: 408px;
      }

      @media (min-width: $md) {
        top: 60%;
        left: -5%;
      }

      @media (min-width: $xl) {
        top: 0;
        left: 10%;
        width: 500px;
        height: 500px;
      }

      &::before {
        content: '';
        position: absolute;
        z-index: 1;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-image: url('../images/profit/ellipse3.png');
        background-size: contain;
        background-repeat: no-repeat;

        @supports (
          background-image: -webkit-image-set(url('../images/profit/ellipse3.webp')
                1x)
        ) {
          background-image: url('../images/profit/ellipse3.webp');
        }
      }
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    max-width: 395px;
    height: auto;
    margin-bottom: 2rem;

    @media (min-width: $xs) {
      margin-bottom: 0;
    }

    @media (min-width: $sm) {
      width: calc(50% - 30px);
      height: 100%;
    }
  }
}
