.title {
  margin-bottom: 54px;
  font-weight: 700;
  font-size: 28px;
  line-height: 110%;
  text-align: center;
  text-transform: uppercase;
  color: inherit;

  @media (min-width: $sm) {
    font-size: 57px;
  }

  @media (min-width: $md) {
    font-size: 60px;
  }

  &--white {
    color: $white-color;
  }
}
