.gallery {
  position: relative;
  padding-top: 50px;
  color: $white-color;

  @media (min-width: $sm) {
    padding-top: 54px;
  }

  @media (min-width: $xl) {
    padding-top: 115px;
  }

  & .container {
    position: relative;
    z-index: 2;
    padding-left: 0;
    padding-right: 0;
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;
    overflow: hidden;

    @media (min-width: $sm) {
      padding: 0;
    }

    // @include beforeBgElement(0, 15%, 0, auto, auto);
    // @include afterBgElement(0, 40%, 0, auto, 50%);

    // @include afterBgElement(0, 90%, 0, auto, 50%);
  }

  &__header {
    display: flex;
    flex-direction: column;
    margin-bottom: 34px;
    padding: 0 15px;

    @media (min-width: $sm) {
      margin-bottom: 44px;
    }

    @media (min-width: $md) {
      margin-bottom: 54px;
      padding-left: 165px;
    }

    & .title {
      text-align: left;
      margin-bottom: 34px;
    }
  }

  &__item {
    position: relative;
    width: 100%;
    overflow: hidden;
    margin: 0 auto;
    margin-bottom: 54px;

    & picture,
    & img {
      width: 100%;
      height: 100%;
    }
  }

  &__title {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 18px;
    line-height: 140%;
    color: $white-color;
    text-transform: uppercase;

    @media (min-width: $sm) {
      font-size: 20px;
      max-width: 640px;
    }

    @media (min-width: $md) {
      padding-left: 165px;
    }

    @media (min-width: $sm) {
      margin-bottom: 44px;
    }

    @media (min-width: $xl) {
      margin-bottom: 33px;
    }

    &--mob {
      display: block;

      @media (min-width: $md) {
        display: none;
      }
    }

    & span {
      color: $primary-color;
    }
  }

  &__scroll {
    display: flex;
    display: grid;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
    column-gap: 10px;
    padding-left: 15px;

    @media (min-width: $md) {
      display: grid;
      padding-left: 0;
    }
  }

  &__pic {
    display: flex;
    overflow: hidden;
    border-radius: 10px;
    width: 262px;
    height: 190px;

    @media (min-width: $sm) {
      width: 524px;
      height: 368px;
    }

    @media (min-width: $md) {
      width: auto;
      height: 300px;
      min-width: 368px;
    }

    & picture {
      @media (min-width: $sm) {
        height: 400px;
      }

      & img {
        height: 100%;

        @media (min-width: $sm) {
          // height: 245px;
          height: 400px;
        }
      }
    }
  }

  &__item--pool {
    & .slider-gallery {
      @media (min-width: $md) {
        margin-left: 165px;
      }
    }
  }

  &__item--spa {
    & .slider-gallery {
      @media (min-width: $md) {
        margin-left: 165px;
      }
    }
  }
}
