.navigation {
  // display: none;
  z-index: 33;
  position: fixed;
  top: 0;
  right: 0;
  width: 495px;
  height: 402px;
  height: 100vh;
  max-width: 100%;
  padding: 30px 27px;
  background-color: $bg-product;
  transform: translateX(100%);
  transition: 0.3s;
  overflow: auto;

  @media (min-width: $xs) {
    padding: 54px;
  }

  &.open {
    display: block;
    transform: translateX(0);
    animation: open-nav 0.3s forwards;
  }

  &.close {
    animation: close-nav 0.1s forwards;
  }

  &__close {
    @include buttonReset(); /* stylelint-disable-line */

    position: absolute;
    top: 20px;
    right: 20px;
    display: block;
    width: 33px;
    height: 33px;
    background: none;
    color: $primary-color;
    opacity: 1;
    transition: 0.5s;

    @media (min-width: $xs) {
      top: 28px;
      right: 28px;
    }

    &:focus,
    &:hover,
    &:active {
      color: $white-color;
      opacity: 1;
    }

    & .icon {
      fill: $primary-color;
    }
  }

  &__header {
    margin-bottom: 34px;
    font-weight: 400;
    font-size: 13px;
    line-height: 100%;
    color: $white-color;
    text-transform: uppercase;

    @media (min-width: $xs) {
      display: none;
    }

    & .logo {
      width: 84px;
      height: 14px;
      margin-bottom: 14px;
    }
  }

  &__list {
    padding-bottom: 16px;
    margin-bottom: 40px;
    border-bottom: 1px solid $white-color;
  }

  &__item {
    margin-bottom: 24px;

    & a {
      font-family: $main-font;
      font-weight: 700;
      font-size: 18px;
      line-height: 130%;
      display: block;
      text-transform: uppercase;
    }
  }
}

.overlay {
  z-index: 11;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  overflow: hidden;
  background-color: rgba(23, 24, 24, 50%);
}

@keyframes open-nav {
  from {
    opacity: 0;
    transform: translateX(100%);
  }

  to {
    opacity: 1;
    transform: translateX(0%);
  }
}

@keyframes close-nav {
  from {
    opacity: 1;
    transform: translateX(0%);
  }

  to {
    opacity: 0;
    transform: translateX(100%);
  }
}
