.messengers {
  z-index: 10;
  position: fixed;
  bottom: 10%;
  right: 1%;
  max-width: 242px;
  background: $white-color;
  border: 1px solid #7a85fb;
  padding: 20px 10px;
  border-radius: 6px;
  color: #171818;

  & .btn-close {
    @include buttonReset(); /* stylelint-disable-line */

    z-index: 3;
    position: absolute;
    top: 10px;
    right: 5px;
    width: 30px;
    height: 30px;
    color: $primary-color;
    opacity: 1;
    background: none;

    &:focus,
    &:hover,
    &:active {
      outline: none;

      &::before {
        background: transparent;
      }
    }
  }

  &__title {
    margin-bottom: 8px;
    font-style: normal;
    font-weight: 700;
    font-size: 13px;
    line-height: 130%;
  }

  &__text {
    margin-bottom: 10px;
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 130%;
  }

  &__upload {
    display: flex;
    justify-content: space-between;

    & span {
      font-style: normal;
      font-weight: 700;
      font-size: 13px;
      line-height: 130%;
    }
  }

  &__links {
    display: flex;
    gap: 10px;
  }

  &--close {
    & .btn-close {
      display: none;
    }

    & .messengers__content {
      display: none;
    }

    & .messengers__upload span {
      display: none;
    }

    & .messengers__links {
      flex-direction: column;
    }

    & .link {
      display: block;
    }
  }
}
