.promo {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: $xxl;
  margin: 0 auto;
  color: $white-color;
  transform-style: inherit;
  overflow: hidden;
  // min-height: 100vh;

  @media (min-width: $sm) {
    // @include beforeBgElement(1, auto, auto, -50%, 20%);
  }

  @media (min-width: $xl) {
    &::after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 10%;
      left: 0;
      width: 300px;
      height: 373px;
      background-image: url('../images/promo/p04.png');
      background-size: contain;
      background-repeat: no-repeat;

      @supports (
        background-image: -webkit-image-set(url('../images/promo/p04.webp') 1x)
      ) {
        background-image: url('../images/promo/p04.webp');
      }

      animation: show-element-pic3 5s forwards;
      // animation: anim 5s infinite;
    }
  }

  & .container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    min-height: 100vh;
    max-width: 1440px;
    margin: 0;
    padding: 50px 15px;
    // padding-top: 24px;
    // padding-bottom: 300px;
    padding-top: 100px;

    @media (min-width: $xs) {
      padding-left: 45px;
      padding-right: 45px;
    }

    @media (min-width: $sm) {
      flex-direction: row;
      padding-top: 62px;
      min-height: 620px;
    }

    @media (min-width: $md) {
      padding-top: 75px;
    }

    @media (max-width: $xs) {
      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        right: 0;
        width: 200px;
        height: 100%;
        background-image: url('../images/promo/ellipse2.webp');
        background-repeat: no-repeat;
        // background: url('../images/promo/ellipse.webp'),
        //   url('../images/promo/woman.webp'), url('../images/promo/ellipse2.webp');
        background-position: 100% 0;
        background-size: contain;

        animation: anim 6s infinite reverse;
      }
    }

    &::before {
      content: '';
      position: absolute;
      top: -5%;
      right: 30%;
      width: 300px;
      height: 100%;
      background-image: url('../images/promo/ellipse.webp');
      background-repeat: no-repeat;
      // background: url('../images/promo/ellipse.webp'),
      //   url('../images/promo/woman.webp'), url('../images/promo/ellipse2.webp');
      background-position: 0% 90%;
      background-size: contain;

      animation: anim 8s infinite;

      @media (min-width: $sm) {
        animation: none;
        top: 0;
        right: 0;
        bottom: auto;
        width: 600px;
        height: 620px;
        background: url('../images/promo/ellipse2.webp'),
          url('../images/promo/woman.webp'), url('../images/promo/ellipse.webp');
        background-position: 60% 100%, 100% 10%, 100% 0;
        background-repeat: no-repeat, no-repeat, no-repeat;
        background-size: 55%, contain, 60%;
      }
    }
  }

  &__info {
    width: 100%;
    z-index: 4;
    position: relative;
    margin-bottom: 40px;
    text-align: center;

    @media (min-width: $xs) {
      text-align: left;
      padding-right: 10%;
    }

    @media (min-width: $xl) {
      padding-left: 230px;
    }
  }

  &__title {
    max-width: 100%;
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    text-transform: uppercase;
    color: $primary-color;

    @include ff(
      'font-size',
      (
        320px: 18px,
        768px: 36px,
        1440px: 43px,
      )
    );

    @media (min-width: $sm) {
      line-height: 140%;
    }

    @media (min-width: $xl) {
      line-height: 110%;
    }
  }

  &__price {
    width: 100%;
    max-width: 100%;
    padding: 0;
    margin-bottom: 38px;
    font-weight: 400;
    font-size: 22px;
    line-height: 100%;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        320px: 22px,
        768px: 36px,
        1200px: 52px,
      )
    );

    & span {
      @include ff(
        'font-size',
        (
          320px: 16px,
          768px: 26px,
          1200px: 32px,
        )
      );
    }
  }

  &__text {
    max-width: 100%;
    margin-bottom: 34px;
    font-weight: 700;
    font-size: 30px;
    line-height: 110%;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        320px: 30px,
        600px: 25px,
        1200px: 67px,
        1440px: 70px,
      )
    );

    @media (min-width: $sm) {
      line-height: 120%;
    }
  }

