.advantages {
  position: relative;
  padding-top: 54px;
  color: $white-color;

  @media (min-width: $sm) {
    padding-top: 0;
    padding-bottom: 54px;
  }

  @media (min-width: $md) {
    background-color: transparent;
    padding-top: 50px;
  }

  & .element-animation.element-show {
    transition-duration: 0.5s;
  }

  & .container {
    z-index: 1;
    position: relative;
    width: 100%;
    max-width: 1110px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 42px;

    @media (min-width: $md) {
      flex-direction: row;
    }

    // @include beforeBgElement(0, -50%, auto, auto, 50%);
  }

  & .title {
    position: relative;
    text-align: left;
    font-weight: 700;
    font-size: 70px;
    line-height: 110%;
    text-transform: uppercase;

    &::before {
      position: absolute;
      z-index: 2;
      content: '';
      top: -60%;
      left: 10%;
      width: 350px;
      height: 298px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      background-image: url('../images/title/hearth.webp');
    }
  }

  &__list {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    width: 100%;
    padding-bottom: 44px;
    margin-bottom: 34px;
    transition: 0.3s;
  }

  &__item {
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    transition: 0.3s;
    padding: 15px;
    border: 1px solid #6ddcff;
    border-radius: 10px;
  }

  &__item-inner {
    position: relative;
    padding-left: 60px;
    transition: 0.3s;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      height: 50px;
      width: 50px;
      transform: translateY(-50%);
      background-image: var(--pic);
      background-repeat: no-repeat;
      background-position: 100% 0;
      background-size: contain;
    }
  }

  &__item-title {
    margin-bottom: 8px;
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
    text-transform: uppercase;
    color: $primary-color;

    @include ff(
      'font-size',
      (
        320px: 18px,
        768px: 20px,
        870px: 16px,
        1200px: 20px,
      )
    );
  }

  &__item-text {
    font-weight: 400;
    font-size: 16px;
    color: $white-color;

    @include ff(
      'font-size',
      (
        320px: 16px,
        500px: 14px,
        768px: 20px,
        870px: 14px,
        1200px: 20px,
      )
    );
  }

  &__info {
    display: inline-flex;
    flex-wrap: wrap;
    padding-left: 22px;
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;

    @include ff(
      'font-size',
      (
        320px: 16px,
        768px: 20px,
      )
    );
  }

  &__blockquote {
    display: inline-flex;
    position: relative;
    font-weight: 700;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: -22px;
      width: 4px;
      height: 100%;
      background-color: $dark-color;
    }
  }

  &__button-pay {
    display: block;
    margin: 0 auto;
  }
}
