*,
::after,
::before {
  box-sizing: border-box;
}

body,
html {
  padding: 0;
  margin: 0;
}

html {
  height: 100%;
}

body,
.page {
  font-family: $main-font;
  font-style: normal;
  font-weight: $medium-font-weight;
  font-size: $main-font-size;
  line-height: $main-line-height;
  background-color: $bg-color;
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

svg {
  fill: currentColor; /* stylelint-disable-line */
}

button[type='submit'] {
  cursor: pointer;

  &:disabled {
    opacity: 0.5;
  }
}

.main {
  padding: 0;
  background-color: $bg-block-gr;
  // background-image: $bg-image-gradient;
  background: radial-gradient(
    at top left,
    $dark-color 0%,
    #1e137e 50%,
    $dark-color 100%
  ), radial-gradient(
    at right top,
    $dark-color 0%,
    #1e137e 100%
  ), radial-gradient(
    at bottom,
    $dark-color 0%,
    #7c76fa 10%,
    #1e137e 50%,
    $dark-color 100%
  );
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@keyframes anim {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-20px);
  }

  100% {
    transform: translateY(0);
  }
}
