.modal {
  font-family: $main-font;

  &-lg {
    max-width: 733px;
  }

  & .title {
    margin-bottom: 28px;
    font-family: $main-font;
    font-style: normal;
    font-weight: 700;
    font-size: 23px;
    line-height: 38px;
    text-align: center;
    letter-spacing: -0.02em;
    color: #101828;

    @media (min-width: $xs) {
      margin-bottom: 48px;
      font-size: 28px;
      line-height: 38px;
    }

    @media (min-width: $sm) {
      margin-bottom: 54px;
      font-size: 36px;
      line-height: 44px;
    }
  }

  & .btn-close.button {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: none;
    color: $primary-color;
    width: 33px;
    height: 33px;
    padding: 0;
    opacity: 1;
    outline: none;

    &:focus,
    &:hover,
    &:active {
      outline: none;

      &::before {
        background: transparent;
      }
    }

    & .icon {
      width: 100%;
      height: 100%;
      stroke: $primary-color;
    }
  }
}

.modal-header {
  padding: 0;
  border: none;
  margin: 0 auto;
}

.modal-body {
  padding: 0;
}

.modal-order {
  & .modal-content {
    // padding: 64px 16px;
    padding: 14vw 5vw 3vw;

    @media (min-width: $sm) {
      // padding: 84px 125px;
      padding: 3vw 5vw;
    }

    @media (min-width: $xl) {
      // padding: 84px 125px;
      padding: 3vw 8vw;
    }

    @media (min-width: $xxl) {
      padding: 84px 125px;
    }

    & .btn-close {
      background: none;
      position: absolute;
      top: 24px;
      right: 24px;
      color: $primary-color;

      & .icon {
        stroke: $primary-color;
      }
    }
  }
}

.modal-message {
  & .modal-content {
    padding: 30px 16px;
    text-align: center;

    & .btn-close {
      background: none;
      position: absolute;
      top: -40px;
      right: 0;
      color: $white-color;
      width: 33px;
      height: 33px;
      padding: 0;
      opacity: 1;

      & .icon {
        stroke: $white-color;
      }
    }
  }
}

.modal-stop {
  & .modal-content {
    padding: 50px 16px 80px;
    text-align: center;

    & .btn-close {
      position: absolute;
      top: 20px;
      right: 20px;
      width: 20px;
      height: 20px;
    }
  }

  & .modal-header,
  & .modal-body {
    max-width: 530px;
    margin: 0 auto;
  }

  & .modal-body {
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: #171818;

    @media (min-width: $xs) {
      font-size: 18px;
    }
  }

  & .title {
    margin-bottom: 33px;
    font-weight: 700;
    font-size: 28px;
    line-height: 36px;
    text-align: center;
    letter-spacing: -0.02em;
    color: #101828;
    text-transform: initial;

    @media (min-width: $xs) {
      font-size: 36px;
      line-height: 44px;
    }
  }

  &__upload {
    margin-top: 33px;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 130%;
    color: #171818;

    @media (min-width: $xs) {
      font-size: 18px;
    }
  }

  &__links {
    margin-top: 33px;
    display: flex;
    justify-content: center;
    gap: 10px;
  }
}
