.product {
  z-index: 3;
  position: relative;
  height: 100%;
  padding: 2px;
  max-width: 395px;
  background-color: $bg-product;
  border-radius: 6px;
  transition: $transition-base;
  color: $white-color;

  &:hover {
    background: radial-gradient(#6ddcff 0%, #7f60f9 30%, transparent 100%);
    transition: $transition-base;
    animation: rotate-position 6s infinite linear;

    @media (min-width: $md) {
      transform: scale(1.05);
    }

    & .button {
      & span {
        background: $bg-gradient;
      }
    }
  }

  &[data-id='1'] {
    background: linear-gradient(98.24deg, #6ddcff 0%, #7f60f9 100%),
      linear-gradient(0deg, #18181b, #18181b);

    &:hover {
      background: radial-gradient(#6ddcff 0%, #7f60f9 30%, transparent 100%);
    }
  }

  &:not([data-id='1']) {
    & .button {
      background: $bg-gradient;

      &::before {
        background: $bg-product;
      }

      // &:focus,
      &:hover,
      &:active {
        background: $bg-product;

        &::before {
          background: $bg-gradient;
        }
      }
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding: 30px 25px;
    border-radius: 6px;
    background-color: $bg-product;

    @media (min-width: $xs) {
      padding: 30px 35px;
    }
  }

  &__type {
    margin-bottom: 5px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;

    @include ff(
      'font-size',
      (
        320px: 18px,
        768px: 18px,
      )
    );

    @media (min-width: $sm) {
      margin-bottom: 14px;
      line-height: 30px;
    }
  }

  &__price {
    margin-bottom: 5px;
    font-weight: 700;
    font-size: 38px;
    line-height: 53px;

    @include ff(
      'font-size',
      (
        320px: 38px,
        768px: 54px,
        1200px: 47px,
        1400px: 60px,
      )
    );

    @media (min-width: $sm) {
      line-height: 60px;
      margin-bottom: 14px;
    }

    @media (min-width: $md) {
      line-height: 28px;
    }

    @media (min-width: $lg) {
      line-height: 66px;
    }

    & span {
      font-weight: 400;
      font-size: 14px;
      line-height: 19px;
      color: #71717a;

      @media (min-width: $sm) {
        font-size: 16px;
        line-height: 22px;
      }

      @media (min-width: $md) {
        line-height: 24px;
      }
    }
  }

  &__text {
    padding-bottom: 16px;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #a1a1aa;
    border-bottom: 1px solid;

    @media (min-width: $lg) {
      margin-bottom: 30px;
    }

    & ul {
      padding-left: 20px;
      list-style-type: disc;
    }
  }

  &__list {
    margin-bottom: 10px;

    @media (min-width: $md) {
      margin-top: auto;
      margin-bottom: 20px;
    }
  }

  &__list li {
    position: relative;
    padding-left: 32px;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;

    @media (min-width: $md) {
      margin-bottom: 20px;
    }

    &::before {
      position: absolute;
      content: '';
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 16px;
      height: 16px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      background-image: url('../images/icons/li.svg');
    }
  }

  & .button {
    & span {
      background: $bg-product;
    }
  }
}

@keyframes rotate-position {
  from {
    background-position: -1000px;
  }

  to {
    background-position: 0;
  }
}
