.faq {
  overflow: hidden;
  position: relative;
  padding-bottom: calc(115px - 27px);
  color: $white-color;
  // background: $bg-block-gr;
  transform: translateY(0%);

  &::before {
    position: absolute;
    content: '';
    z-index: 10;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100px;
    background-image: linear-gradient(
      179.97deg,
      rgba(5, 10, 52, 0%) 11.31%,
      #030619 33.51%,
      #020202 99.98%
    );
  }

  &__wrapper {
    position: relative;
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;
    min-height: 700px;
  }

  & .container {
    position: relative;
    max-width: 1110px;
    padding: 0 15px;
    margin: 0 auto;
    margin-bottom: 50px;

    @media (min-width: $sm) {
      padding-top: 50px;
    }

    @media (min-width: $md) {
      padding-left: 100px;
      margin-top: 100px;
      margin-bottom: 100px;
    }
  }

  & .title {
    position: relative;
    z-index: 1;
    text-align: center;

    @media (min-width: $md) {
      text-align: left;
    }
  }

  &__bg {
    position: relative;
    width: 100%;
    height: 250px;
    max-width: 100%;

    @media (min-width: $xs) {
      height: 400px;
    }

    @media (min-width: $md) {
      position: absolute;
      top: -10%;
      right: 0;
      width: 50%;
      height: 800px;
    }

    &--1 {
      z-index: 3;
      position: absolute;
      top: -15%;
      right: -10%;
      width: 764px;
      height: 300px;
      max-width: 100%;
      background-image: url('../images/faq/men.png');
      background-size: contain;
      background-repeat: no-repeat;

      @supports (
        background-image: -webkit-image-set(url('../images/faq/men.webp') 1x)
      ) {
        background-image: url('../images/faq/men.webp');
      }

      @media (min-width: $xs) {
        height: 400px;
        top: 10%;
        right: -30%;
      }

      @media (min-width: $md) {
        // position: fixed;
        height: 640px;
        top: auto;
        bottom: -50px;
        right: -5%;
      }

      @media (min-width: 2000px) {
        bottom: 0;
        right: 6%;
      }
    }

    // &--2 {
    //   position: absolute;
    //   top: 0;
    //   right: 0;
    //   width: 938px;
    //   height: 636px;
    //   max-width: 100%;
    //   background: radial-gradient(
    //     50% 50% at 50% 50%,
    //     #071053 40.76%,
    //     #020c56 76.56%,
    //     $dark-color 98.44%
    //   );
    //   filter: blur(162px);
    // }
  }

  &__list {
    position: relative;
    z-index: 5;
    margin-top: 27px;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    max-width: 624px;
    border-top: 1px solid $white-color;
    font-weight: 400;
    font-size: 15px;
    line-height: 130%;
    color: $white-color;

    @include ff(
      'font-size',
      (
        320px: 16px,
        670px: 20px,
        1280px: 15px,
      )
    );

    @media (min-width: $md) {
      margin-right: auto;
      margin-left: 0;
    }
  }
}
